import Glide from '@glidejs/glide'
import smoothScroll from 'smoothscroll'

const clients = new Glide('section#section-1 .glide', {
  perView: 3,
  keyboard: true,
  autoplay: 9000
})
clients.mount()
document.querySelectorAll('section#section-1 .arrow')[0].addEventListener('click', () => {
  clients.go('<')
})
document.querySelectorAll('section#section-1 .arrow')[1].addEventListener('click', () => {
  clients.go('>')
})

const ordering = new Glide('section#section-2 .glide', {
  perView: 1,
  startAt: 0,
})
ordering.mount()
ordering.disable()
document.querySelectorAll('section#section-2 .mega-checkbox')[0].querySelectorAll('label').forEach(checkbox =>
  checkbox.addEventListener('click', () => {
    ordering.enable()
    ordering.go('=1')
    setTimeout(() => ordering.disable(), 400)
  }))
document.querySelector('section#section-2 #go-to-step3').addEventListener('click', (event) => {
  event.preventDefault()
  ordering.enable()
  ordering.go('=2')
  setTimeout(() => ordering.disable(), 400)
})
document.querySelector('section#section-2 #go-to-step4').addEventListener('click', (event) => {
  event.preventDefault()
  ordering.enable()
  ordering.go('=3')
  setTimeout(() => ordering.disable(), 400)
})

const constructions = new Glide('section#section-6 .glide', {
  perView: 1,
  autoplay: false
})
constructions.mount()
document.querySelectorAll('section#section-6 .tabs > div').forEach(tab => tab.addEventListener('click', () => {
  constructions.go(`=${tab.getAttribute('tab-id')}`)
}))
constructions.on('move', (event) => {
  document.querySelectorAll('section#section-6 .tabs > div.active').forEach(tab => tab.classList.remove('active'))
  document.querySelectorAll('section#section-6 .tabs > div')[constructions._i].classList.add('active')
})

const cases = new Glide('section#section-11 .glide', {
  perView: 1,
  autoplay: false
})
cases.mount()
document.querySelectorAll('section#section-1 .glide__slide').forEach(slide => slide.addEventListener('click', () => {
  cases.go(`=${slide.getAttribute('case-index')}`)
  smoothScroll(document.querySelector('section#section-11'))
}))

const recommendations = new Glide('section#section-13 .glide', {
  perView: 3,
  keyboard: true,
  autoplay: 9000
})
recommendations.mount()
document.querySelectorAll('section#section-13 .arrow')[0].addEventListener('click', () => {
  recommendations.go('<')
})
document.querySelectorAll('section#section-13 .arrow')[1].addEventListener('click', () => {
  recommendations.go('>')
})

document.querySelectorAll('.mega-checkbox').forEach(container => {
  const input = container.querySelector('input[type="hidden"]')
  container.querySelectorAll('label').forEach(checkbox => {
    checkbox.insertAdjacentHTML('beforeEnd', `<div class="checkbox-flag" style="top: ${(checkbox.offsetHeight - 36) / 2}px"></div>`)
    checkbox.addEventListener('click', () => {
      if (container.getAttribute('checkbox-type') === 'radio') {
        input.value = checkbox.textContent
        container.querySelectorAll('label.active').forEach(checkbox => {
          checkbox.classList.remove('active')
        })
      }
      checkbox.classList.toggle('active')
      if (container.getAttribute('checkbox-type') !== 'radio') {
        let text = ''
        container.querySelectorAll('label.active').forEach(checkbox => {
          text += checkbox.textContent + '; '
        })
        if (text !== '')
          input.value = text
        else
          input.value = '-'
      }
      checkbox.classList.add('small-icon')
      setTimeout(() => checkbox.classList.remove('small-icon'), 100)
    })
  })
})


const sections = document.querySelectorAll('section')
const html = document.querySelector('html')
let lastScrollPosition = 0
const scrollHandler = () => {
  // if (lastScrollPosition < html.scrollTop)
  //   console.log('scroll down')
  // else
  //   console.log('scroll up')
  lastScrollPosition = html.scrollTop
  document.querySelectorAll(`.section-hints > .hint.active`).forEach(hint => hint.classList.remove('active'))
  for (let section of sections) {
    if (section.offsetTop + section.offsetHeight > html.scrollTop + (screen.height / 2)) {
      document.querySelector(`.section-hints > .hint[data-section="${section.getAttribute('id').replace('section-', '') * 1}"]`).classList.add('active')
      break
    }
  }
}
document.querySelectorAll(`.section-hints > .hint`).forEach(hint => hint.addEventListener('click', () => {
  smoothScroll(document.querySelector(`#section-${hint.getAttribute('data-section')}`))
}))
window.addEventListener('scroll', scrollHandler)

document.querySelector('#main-menu .button').addEventListener('click', () => 
  document.querySelector('#main-menu').classList.toggle('visible'))
document.querySelector('#main-menu .overlay').addEventListener('click', () => 
  document.querySelector('#main-menu').classList.remove('visible'))

document.querySelectorAll('.modal-wrapper').forEach(wrapper => wrapper.addEventListener('click', (event) => {
  if (event.target.classList.contains('modal-wrapper'))
    wrapper.classList.remove('visible')
}))


// update!
document.querySelectorAll('form').forEach(form => form.addEventListener('submit', event => {
  event.preventDefault()
  document.querySelectorAll('.modal-wrapper.visible').forEach(modal => modal.classList.remove('visible'))
  if (form.classList.contains('models-form'))
    document.querySelector('#model').classList.add('visible')
  else
    document.querySelector('#thanks').classList.add('visible')
}))
document.querySelector('.very-big-button').addEventListener('click', () => document.querySelector('#special-for-managers').classList.add('visible'))

document.querySelectorAll('#section-11 button').forEach(button => button.addEventListener('click', () => document.querySelector('#task-solving').classList.add('visible')))

document.querySelector('.stop-overpay').addEventListener('click', () => document.querySelector('#choise').classList.add('visible'))

document.querySelectorAll('button.upload-btn').forEach(btn => btn.onclick = () => false)

document.querySelector('footer button').addEventListener('click', () => document.querySelector('#contact').classList.add('visible'))